.about-journey-section {
  padding: 80px 60px;
  background: #ffffff;
}

.about-top {
  display: flex;
  align-items: flex-start;
  gap: 40px;
  margin-bottom: 80px;
      margin-top: -180px;
}

.about-top-img img {
  width: 100%;
  border-radius: 20px;
}

.about-top-card {
  background: #f9faff;
  padding: 30px;
  border-radius: 20px;
  max-width: 400px;
  box-shadow: 0 20px 45px rgba(0,0,0,0.08);
}

.about-top-card h3 {
  font-size: 22px;
  font-weight: 700;
  color: #223;
  margin-bottom: 12px;
}

.about-top-card p {
  font-size: 15px;
  color: #555;
  line-height: 1.6;
  margin-bottom: 20px;
}

.about-members {
  display: flex;
  align-items: center;
  gap: 10px;
}

.about-members img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.about-members span {
  font-size: 14px;
  font-weight: 500;
  color: #333;
}



.story-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 120px 10%;
  align-items: center;
  background: #ffffff;
  font-family: "Poppins", sans-serif;
}

.story-left h2 {
  font-size: 48px;
  font-weight: 600;
  line-height: 1.2;
  color: #07105a;
}

.story-right p {
  font-size: 18px;
  line-height: 1.7;
  color: #07105a;
      max-width: 822px;
}



.about-lower {
  display: flex;
  align-items: center;
  gap: 50px;
}

.lower-img img {
  width: 95%;
  border-radius: 20px;
}

.lower-content {
  max-width: 650px;
}

.lower-tag {
  background: #eef1ff;
  padding: 6px 14px;
  border-radius: 18px;
  font-size: 13px;
  font-weight: 600;
  color: #3b3b98;
  display: inline-block;
  margin-bottom: 18px;
}

.lower-title {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 18px;
}

.lower-desc {
  font-size: 16px;
  color: #555;
  line-height: 1.7;
  margin-bottom: 20px;
}

.lower-list li {
  font-size: 16px;
  color: #333;
  margin-bottom: 6px;
}

.visit-btn {
  margin-top: 25px;
  padding: 12px 22px;
  font-size: 16px;
  background: #2b47fc;
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  transition: 0.3s;
}

.journey-container {
    display: flex;
    gap: 80px;
    padding-bottom: 100px;
    align-items: center;
    margin-left: 20px;
}


@media (max-width: 992px) {
  .about-top {
    flex-direction: column;
  }
  .about-lower {
    flex-direction: column;
  }
}

.new-div-join {
    margin-top: 275px;
}

@media (max-width: 576px) {
  .about-journey-section {
    padding: 40px 25px;
  }
  .journey-title {
    font-size: 28px;
  }
  .lower-title {
    font-size: 28px;
  }
}
